<template>
  <div id="App">
    <nav class="">
      <p>{{ Translated.header }}</p>
      <div>
        <button
          class="language-btn"
          v-if="(temp = 0)"
          variant="success"
          @click="lang = 'Eng'"
        >
          Eng
        </button>
        <button
          class="language-btn"
          v-if="(temp = 1)"
          variant="success"
          @click="lang = 'Ru'"
        >
          Ru
        </button>
        <button
          class="language-btn"
          v-if="(temp = 2)"
          variant="success"
          @click="lang = 'Uz'"
        >
          Uz
        </button>
      </div>
    </nav>
    <h1>{{ Translated.header }}</h1>
    <p>{{ Translated.text }}</p>
    <div class="sliderr">
      <button class="slider" @click="prevImage()">◄</button>
      <img v-if="imageIndex == 0" src="./assets/anime1.jpg" alt="" />
      <img v-if="imageIndex == 1" src="./assets/anime2.jpg" alt="" />
      <img v-if="imageIndex == 2" src="./assets/anime3.jpg" alt="" />
      <button class="slider" @click="nextImage()">►</button>
    </div>
  </div>
</template>

<script>
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";

export default {
  name: "App",
  data() {
    return {
      lang: "Eng",
      imageIndex: 0,
      response: {
        header: {
          Eng: "We Are SomeCompany",
          Ru: "Мы - SomeCompany",
          Uz: "Biz - SomeCompany",
        },
        text: {
          Eng: "and we do some crazy stuff like managment or smth.",
          Ru: "и мы делаем нереальные вещи по типу менеджмент или что то там ещею",
          Uz: "Va biz tasavvurga sig'maydigon narsalar bilan shug'ullanamiz, masalan menedjment",
        },
      },
    };
  },
  methods: {
    nextImage() {
      if (this.imageIndex < 2) this.imageIndex++;
      else this.imageIndex = 0;
    },
    prevImage() {
      if (this.imageIndex > 0) this.imageIndex--;
      else this.imageIndex = 2;
    },
  },
  computed: {
    Translated() {
      let temp = Object.assign({}, this.response);
      temp.header = temp.header[this.lang];
      temp.text = temp.text[this.lang];
      return temp;
    },
  },
};
</script>

<style>
nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
#App {
  text-align: center;
  color: rgb(73, 73, 73);
}
.sliderr img {
  height: 50%;
  width: 50%;
}
.language-btn {
  width: 80px;
  outline: none;
  background: transparent;
  border: none;
  margin: 0 5px;
  position: relative;
}
.language-btn::after {
  content: "";
  position: absolute;
  display: block;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: green;
  transform: scale(0);
}
.language-btn:hover:after {
  transition: 0.6s;
  transform: scale(1);
}
.slider {
  background: transparent;
  outline: none;
  border: none;
  font-size: 32px;
  margin: 0 5px;
}
.slider:hover {
  color: crimson;
}
</style>
